import Hammer from 'hammerjs'
export default {
  mounted() {
    let square = this.$refs.page;
    var hammer = new Hammer(square);
    hammer.on('swiperight', (e) => {
      this.$router.push('/shoucang');
      this.$store.commit('layout/setPageActive',1);
    });
  },
}



